p {
  margin: 0;
}
.catalog {
  padding-top: 40px;
}
.catalog-container {
  margin-left: auto;
  margin-right: auto;
  width: 320px;
  padding-left: 20px;
  padding-right: 20px;
}
.search-form {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-bottom: 16px;
}

.search-form input {
  padding-left: 20px;
  width: 224px;
  height: 40px;
  border: 1px solid var(--white);
  border-radius: 8px;
  background: transparent;
  color: var(--white);
}

.search-form input::placeholder {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 14px;
  line-height: calc(16 / 14);

  color: #b7b7b7;
}

.search-form-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;

  cursor: pointer;

  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 50%;
  border: none;
}

.search-form-icon {
  stroke: #f84119;
}

.search-form-btn:hover {
  border: 1px solid #f84119;
  background: transparent;
  fill: #ffc226;
}

.catalog-list {
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  padding: 0;
  list-style-type: none;
}

.catalog-item {
  position: relative;
  width: 280px;
  height: 406px;
}

.image-wrap {
  width: 100%;
  height: 100%;
}
.photo-card {
  height: inherit;
}

.photo-card img {
  height: inherit;
  width: 100%;
  object-fit: cover;
  background-repeat: no-repeat;
}

.catalog-rating:before {
  width: fit-content !important;
  height: fit-content !important;
  top: 372.88px !important;
  left: 182.44px !important;
}

.image-wrap:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 63.48%,
    rgba(0, 0, 0, 0.9) 92.16%
  );
}

.film-info {
  max-width: 60%;
  position: absolute;
  left: 12px;
  bottom: 12px;

  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: calc(20 / 12);

  color: #ffffff;
}

.info-wrap {
  display: flex;
  gap: 5px;
  color: #b7b7b7;
}

.tui-pagination {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.tui-page-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;

  border: 1px solid #595959;
  border-radius: 50%;

  font-family: 'Roboto';
  font-weight: 400;
  font-size: 10px;
  line-height: calc(10 / 10);

  color: var(--white);
}

.tui-is-selected {
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
}

.tui-page-btn.tui-prev {
  background-image: url(../images/pagination/pagination-left.svg);
  background-position: 50%;
  background-repeat: no-repeat;
  border: none;
  margin-right: 0;
}

.tui-page-btn.tui-prev:hover {
  stroke: #b7b7b7;
}

.tui-page-btn.tui-next {
  background-image: url(../images/pagination/pagination-right.svg);
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  fill: #b7b7b7;
  margin-right: 0;
}

.tui-page-btn.tui-first {
  background-image: url(../images/pagination/double-arrow-prev.png);
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  margin: 0;
  fill: #b7b7b7;
}

.tui-page-btn.tui-last {
  background-image: url(../images/pagination/double-arrow-next.png);
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  margin: 0;
  fill: #b7b7b7;
}

.no-films-message {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  /* gap: 8px; */

  font-family: 'Roboto';
  font-size: 20px;
  line-height: calc(28 / 20);

  color: var(--white);
}

@media screen and (min-width: 768px) {
  .catalog-container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
  .search-form {
    margin-bottom: 36px;
  }
  .search-form input {
    width: 204px;
  }
  .catalog-list {
    row-gap: 28px;
  }
  .catalog-item {
    width: 224px;
    height: 325px;
  }
  .catalog-rating:before {
    top: 293.88px !important;
    left: 133.44px !important;
  }
  .film-info {
    line-height: calc(18 / 12);
  }
  .tui-pagination {
    gap: 16px;
  }
  .tui-page-btn {
    width: 40px;
    height: 40px;

    font-size: 14px;
  }
  .no-films-message {
    font-size: 24px;
    line-height: calc(32 / 24);
  }
}

@media screen and (min-width: 1280px) {
  .catalog-container {
    width: 1280px;
  }
  .search-form {
    margin-bottom: 48px;
  }
  .search-form input {
    width: 334px;
    padding-left: 24px;
  }
  .search-form input::placeholder {
    font-size: 16px;
    line-height: calc(19 / 16);
  }
  .catalog-item {
    width: 394px;
    height: 574px;
  }
  .film-info {
    bottom: 20px;
    left: 20px;
    font-size: 20px;
    line-height: calc(23 / 20);
  }
  .film-title {
    margin-bottom: 12px;
  }
  .catalog-list {
    row-gap: 32px;
  }
  .no-films-message {
    font-size: 32px;
    line-height: calc(36 / 32);
  }
  .catalog-rating:before {
    top: 533.88px !important;
    left: 282.44px !important;
  }
}
